const getButtonTitle = async (userId) => {

    let strTitle = "";

    const url = `https://pht.life/api/v1/member/util/button/`;

    const reqOptions = {
        "userId": userId
    }

    const options = {
        method: "POST",
        body: JSON.stringify(reqOptions),
        headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            "X-Requested-With": "XMLHttpRequest",
        },
        mode: 'cors',
    }

    await fetch(url, options)
        .then((response) => response.json())
        .then((data) => {
            strTitle = data.result;
        })
        .catch((error) => console.log(error));

    return strTitle;
}

export default getButtonTitle;